.portfolioContainer {
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    width: 100%;
  
    .portfolioHeading {
      font-size: 3rem;
      text-align: center;
      color: #fff;
      margin-bottom: 20px;
    }
  
    .projects {
      display: flex;
      justify-content: center;
      width: 100%;
      padding-top: 5rem;
  
      @media screen and (max-width: 1500px) {
        align-items: center;
        flex-direction: column;
      }
  
      .project {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 50%;
        margin-bottom: 3rem;
  
        img {
          width: 700px;
          height: 400px;
  
          @media screen and (max-width: 800px) {
            width: 400px;
            height: 200px;
          }
        }
  
        span {
          display: inline-block;
          margin-top: 1rem;
          font-size: 1rem;
          color: #fff;
          text-align: center;
          width: 90%;
        }
      }
    }
  }